html {
  font-size: 100%;
}

body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
}

a {
  display: inline-block;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

:root {
  --Pink: hsl(322, 100%, 66%);
  --white: hsl(0, 0%, 100%);
  --Very-Pale-Cyan: hsl(193, 100%, 96%);
  --Very-Dark-Cyan: hsl(192, 100%, 9%);
  --Grayish-Blue: hsl(208, 11%, 55%);
}

:root {
  --ff-sans: "Open Sans", sans-serif;
  --ff-poppins: "Poppins", sans-serif;
}

.container {
  width: min(90%, 90rem);
  margin-inline: auto;
}
.container--large {
  width: min(80%, 77.5rem);
  margin-inline: auto;
}

.navbar__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 56.25rem) {
  .flex {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}

body {
  font-family: var(--ff-sans);
  font-weight: 400;
  font-size: 1.125rem;
  text-align: center;
}

h1,
h2,
h3 {
  margin-bottom: 0;
  font-family: var(--ff-poppins);
}

p {
  margin-bottom: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.button {
  font-weight: 700;
  background-color: var(--Pink);
  color: var(--Very-Pale-Cyan);
  padding: 1em 3em;
  border-radius: 6.25rem;
}

.navbar__link--button {
  padding: 0.75em 1.5em;
  background-color: var(--white);
  box-shadow: 0px 8px 24px hsla(210deg, 8%, 62%, 0.2);
  border-radius: 6.25rem;
}

.card {
  padding: 2em;
  background-color: var(--white);
  box-shadow: 0px 8px 24px hsla(210deg, 8%, 62%, 0.2);
  border-radius: 0.625rem;
}

.card + .card {
  margin-top: 3rem;
}

.footer {
  padding-block: 3em;
  background-color: var(--Very-Dark-Cyan);
  color: var(--Very-Pale-Cyan);
}

.header {
  background-color: var(--Very-Pale-Cyan);
  background-image: url("../../../images/bg-hero-mobile.svg");
  padding-bottom: 4em;
}

.navbar {
  padding-block: 2rem;
}

.navbar__image {
  width: 12.5rem;
}

.main {
  padding-block: 4em;
  background-color: var(--Very-Pale-Cyan);
}