@use "../utilities" as *;

body {
  font-family: var(--ff-sans);
  font-weight: 400;
  font-size: rem(18);
  text-align: center;
}

h1,
h2,
h3 {
  margin-bottom: 0;
  font-family: var(--ff-poppins);
}

p {
  margin-bottom: 0;
}

a {
  text-decoration: none;
  color: inherit;
}
